








































import { Vue, Component, Prop } from 'vue-property-decorator';
import { mapMutations } from 'vuex';

@Component({
  methods: {
    ...mapMutations('colors', ['SWITCH_COLOR', 'RESET_COLOR']),
  },
})
export default class Dialog extends Vue {
  @Prop() blockColor!: object;

  dialog = false;

  selectBlock = '';

  selectColor = '';
}
